/*
*   Name: BizAgi Desktop List Cases implementation
*   Author: Kevin Alvarado
*/

bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.listCases", {}, {
    /*
    *   Returns the widget name
    */
    getWidgetName: function () {
        return bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_LISTCASES;
    },
    
    /**
    * Set the table with the fixed thead
    * @param {Json} params TableElement,groupByCaseNumber,floatOptions
    */
    setTableHeadFixed:function(params) {
        params.TableElement.floatThead(params.floatOptions);
        
        if(params.groupByCaseNumber && bizagi.util.isIE()){
            params.TableElement.find("tbody > tr.ui-bizagi-wp-app-inbox-grouped-trtag-hide").hide();
        }
    }
});
