// Extends itself
$.Class.extend("bizagi.workportal.inbox.rowsPerPage", {

}, {

    
    /**
     * return the combo DataSource with the default values and bizagiConfig.inboxRowsPerPage
     */
    initCombo: function(){
        var comboDataSource = {
            combo: [],
            id: "numberRows"
        };
        var values=Array(10,20,30,40,50,100);
        if(bizagiConfig.inboxRowsPerPage && values.indexOf(bizagiConfig.inboxRowsPerPage)==-1){
            values.push(bizagiConfig.inboxRowsPerPage);
            values.sort( function(a, b) {return a-b} );
        }
        values.map(function(v) {
            comboDataSource.combo.push({
                id: v,
                name: v
            });
        });
        return comboDataSource;
    },
    /**
     * Busca el valor finding en el combo[i].id si no lo encuentra retorna false, de lo contrario retorna el elemento combo[i]
     * @param {Array} combo array de valores del combo
     * @param {Number} finding valor que se busca
     */
    _findValue: function (combo,finding) {
        var value=false;
        combo.map(function (c) {
            if(c.id==finding){
                value=c;
            }
        });
        return value;
    },

    /**
     * Revisa el valor inicial que debe tener el combo, revisando preferencias de usuario, web.config y casos null, retorna un elemento del combo
     * @param {JSON} params contenido del comboDataSource, setRowValue y getRowValue
     */
    initialValueCombo: function (params) {
        var self = this;
        var initValueCombo = params.comboDataSource.combo[0];
        var preferencesItemsPerPage=params.getRowValue();
        preferencesItemsPerPage=(preferencesItemsPerPage && preferencesItemsPerPage.inboxItemsPerPage)?preferencesItemsPerPage.inboxItemsPerPage:null;
        if(preferencesItemsPerPage || bizagiConfig.inboxRowsPerPage){
            var defaultValue=(preferencesItemsPerPage && self._findValue(params.comboDataSource.combo, preferencesItemsPerPage)!==false)?preferencesItemsPerPage:bizagiConfig.inboxRowsPerPage;
            if(defaultValue){
                initValueCombo=self._findValue(params.comboDataSource.combo, defaultValue);
            }
        }
        return initValueCombo;
    },

    /**
     * Agrega la interacción al comboBox y define los listeners, retorna el deferred
     * @param {*} params combo, content, comboWrapper, onChange, setRowValue y getRowValue
     */
    renderNumberRowsCombo: function (params) {
        var self = this;
        var deferred = $.Deferred();
        params.comboWrapper.empty();
        var initValueCombo=self.initialValueCombo({
            comboDataSource: params.combo,
            setRowValue: params.setRowValue,
            getRowValue: params.getRowValue
        });
        var lastValue=initValueCombo.id;
        params.comboWrapper.uicombo({
            isEditable: false,
            data: params.combo,
            itemValue: function (obj) {
                return obj.id;
            },
            itemText: function (obj) {
                return obj.name;
            },
            onComplete: function () {
                deferred.resolve();
            },
            onChange: function (obj) {
                var value = obj.ui.data('value');
                if(value!=lastValue){
                    lastValue=value;
                    $("#userPositionInput", params.content).val(value);
                    params.setRowValue(value); //Store the value of rows
                    params.onChange();
                }
            },
            initValue: initValueCombo
        });
        return deferred;
    },
});
