

bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.integration", {}, {
    init: function (workportalFacade, dataService, params) {
        var self = this;
        self.module = params.module || null;
        self.selector = params.selector || "";   
    },

    getWidgetName: function () {
        return bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_INTEGRATION;
    },

    renderContent: function () {
        var self = this;
        var selector = document.createElement(self.selector);
        var content = self.content = $(selector);

        return content
    },
    postRender: function () {
        var self = this;
        //Load module
        if (self.module) {
            bizagi.loader.start(self.module).then(function () {
           });
        }
    },

    clean: function () {
        var self = this;
        $(self.selector).empty()

        self._super();
    }
});